<template>
    <div class="searchAriticle">
        <!-- header -->
        <Vheader v-on:cleartAdvancedParams="cleartAdvancedParams" rightBtn="menu" headText="搜索"></Vheader>
        <!-- main -->
        <article class="main">
            <!-- search_result -->
            <div class="searchResult_wraper" v-if="selectedParams.length>0">
                <ul class="selectedParams">
                    <!-- 姓名选中 -->
                    <li v-if="formItem.nameReturn!=''">
                        {{formItem.nameReturn}}
                        <span @click="removeSelected(formItem.nameReturn,'nameReturn')" class="close_">
                            <Icon type="close"></Icon>
                        </span>
                    </li>
                    <!-- 性别选中 -->
                    <li v-if="formItem.value.xbReturn!=''">
                        {{formItem.value.xbReturn}}
                        <span @click="removeSelected(formItem.value.xbReturn,'xbReturn')" class="close_">
                            <Icon type="close"></Icon>
                        </span>
                    </li>
                    <!-- 年龄选中 -->
                    <li v-if="formItem.age.ageReturn!=''">
                        {{formItem.age.ageReturn}}
                        <span @click="removeSelected(formItem.age.ageReturn,'ageReturn')" class="close_">
                            <Icon type="close"></Icon>
                        </span>
                    </li>
                    <!-- 国籍选中 -->
                    <li v-if="formItem.value.gj.length>0" v-for="(v) in formItem.value.gj" :key="v">
                        {{v| filterNum}}
                        <span @click="removeSelected(v,'gj')" class="close_">
                            <Icon type="close"></Icon>
                        </span>
                    </li>
                    <!-- 单位选中 -->
                    <li v-if="formItem.value.dw.length>0" v-for="(v) in formItem.value.dw" :key="v">
                        {{v | filterNum}}
                        <span @click="removeSelected(v,'dw')" class="close_">
                            <Icon type="close"></Icon>
                        </span>
                    </li>
                    <!-- 职称选中 -->
                    <li v-if="formItem.value.zc.length>0" v-for="(v) in formItem.value.zc" :key="v">
                        {{v | filterNum}}
                        <span @click="removeSelected(v,'zc')" class="close_">
                            <Icon type="close"></Icon>
                        </span>
                    </li>
                    <!-- 职称学位 -->
                    <li v-if="formItem.value.xw.length>0" v-for="(v) in formItem.value.xw" :key="v">
                        {{v | filterNum}}
                        <span @click="removeSelected(v,'xw')" class="close_">
                            <Icon type="close"></Icon>
                        </span>
                    </li>
                    <!-- 学科选中 -->
                    <li v-if="formItem.value.xk.length>0" v-for="(v) in formItem.value.xk" :key="v">
                        {{v | filterNum}}
                        <span @click="removeSelected(v,'xk')" class="close_">
                            <Icon type="close"></Icon>
                        </span>
                    </li>

                    <!-- 职称籍贯 -->
                    <li v-for="(v,i) in formItem.value.jgformat" :key="i">
                        {{v | filterNum}}
                        <span @click="removeSelected(v,'jg')" class="close_">
                            <Icon type="close"></Icon>
                        </span>
                    </li>

                </ul>
            </div>
            <transition name="right-animate">
                <div class="rightMenu" v-if="getShowMenu">
                    <div class="title">
                        <h2>{{lang.advanced_search}}</h2>
                    </div>
                    <div class="condition_search">
                        <i-form :model="formItem" class="test">
                            <!-- 姓氏 -->
                            <Form-item>
                                <div class="group" ref="xx">
                                    <div class="condition_name">
                                        <h5>{{lang.name}}</h5>
                                    </div>
                                    <div class="condition_list">
                                        <div class="condition_wrap xb_style">
                                            <input type="text" class="xm_input" v-model="formItem.name">
                                        </div>
                                    </div>
                                </div>

                            </Form-item>
                            <!-- 性别 -->
                            <Form-item>
                                <div class="group" ref="xb">
                                    <div class="condition_name">
                                        <h5>{{lang.gender}}</h5>
                                    </div>
                                    <div class="condition_list">
                                        <div class="condition_wrap">
                                            <RadioGroup v-model="formItem.value.xb" type="button">
                                                <Radio class="xb_css" label="3">
                                                    <span>{{lang.all}}</span>
                                                </Radio>
                                                <Radio class="xb_css" label="1">
                                                    <span>{{lang.male}}</span>
                                                </Radio>
                                                <Radio class="xb_css" label="2">
                                                    <span>{{lang.female}}</span>
                                                </Radio>

                                            </RadioGroup>
                                        </div>
                                    </div>
                                </div>
                            </Form-item>
                            <!-- 年龄 -->
                            <Form-item>
                                <div class="group" ref="nl">
                                    <div class="condition_name">
                                        <h5>{{lang.age}}</h5>
                                    </div>
                                    <div class="condition_list">
                                        <div class="condition_wrap">
                                            <div class="age">
                                                <input type="text" v-model="formItem.age.start"> -
                                                <input type="text" v-model="formItem.age.end">
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Form-item>
                            <!-- 国籍 -->
                            <Form-item>
                                <div class="group" ref="gj">
                                    <div class="condition_name">
                                        <h5>{{lang.search_country}}</h5>
                                    </div>
                                    <div class="condition_list">
                                        <div class="condition_wrap commonInput_style" id="gj">
                                            <Select v-model="formItem.value.gj" multiple filterable remote :remote-method="remoteMethod" :loading="loading" placeholder="">
                                                <Option v-for="item in optionsgj" :value="item.country_code+'-'+item.country_name" :key="item.country_code">{{ item.country_name }}</Option>
                                            </Select>
                                        </div>
                                    </div>
                                </div>
                            </Form-item>
                            <!-- 单位 -->
                            <Form-item>
                                <div class="group" ref="dw">
                                    <div class="condition_name">
                                        <h5>{{lang.unit}}</h5>
                                    </div>
                                    <div class="condition_list">
                                        <div class="condition_wrap commonInput_style" id="dw">
                                            <Select v-model="formItem.value.dw" multiple filterable remote :remote-method="remoteColleges" :loading="loading" placeholder="">
                                                <Option v-for="item  in optionsdw" :value="item.collegeId+'-'+item.collegeName" :key="item.id">{{ item.collegeName}}</Option>
                                            </Select>
                                        </div>
                                    </div>
                                </div>
                            </Form-item>
                            <!-- 职称 -->
                            <Form-item>
                                <div class="group" ref="zc">
                                    <div class="condition_name">
                                        <h5>{{lang.job_title}}</h5>
                                    </div>
                                    <div class="condition_list">
                                        <div class="condition_wrap commonlable_style">
                                            <Checkbox-group v-model="formItem.value.zc">
                                                <Checkbox :label="v.codekey+'-'+v.codename" v-for="(v,i) in  getworkTitles" :key="i">{{v.codename}}</Checkbox>
                                            </Checkbox-group>
                                        </div>
                                    </div>
                                </div>
                            </Form-item>
                            <!-- 学位 -->
                            <Form-item>
                                <div class="group" ref="xw">
                                    <div class="condition_name">
                                        <h5>{{lang.degree}}</h5>
                                    </div>
                                    <div class="condition_list">
                                        <div class="condition_wrap  commonlable_style">
                                            <Checkbox-group v-model="formItem.value.xw">
                                                <Checkbox :label="v.codekey+'-'+v.codename" v-for="(v,i) in getlearnDegrees" :key="i">{{v.codename}}</Checkbox>
                                            </Checkbox-group>
                                        </div>
                                    </div>
                                </div>
                            </Form-item>
                            <!-- 学科 -->
                            <Form-item>
                                <div class="group" ref="xk">
                                    <div class="condition_name">
                                        <h5>{{lang.subject}}</h5>
                                    </div>
                                    <div class="condition_list">
                                        <div class="condition_wrap commonlable_style">
                                            <Checkbox-group v-model="formItem.value.xk">
                                                <Checkbox :label="v.codekey+'-'+v.codename" v-for="(v,i) in getsubjects" :key="i">{{v.codename}}</Checkbox>
                                            </Checkbox-group>
                                        </div>
                                    </div>
                                </div>
                            </Form-item>
                            <!-- 籍贯 -->
                            <Form-item>
                                <div class="group" ref="jg">
                                    <div class="condition_name">
                                        <h5>{{lang.origin}}</h5>
                                    </div>
                                    <div class="condition_list">
                                        <div class="condition_wrap" id="jg">
                                            <div class="group" id="group_sj">
                                                <Select v-model="formItem.jgB" filterable @on-change="getArea_A">
                                                    <Option v-for="(item,index) in getControllerA" :value="item.area_id+'-'+item.area_name" :key="'shengji'+index">{{ item.area_name}}</Option>
                                                </Select>
                                            </div>
                                            <div class="group">
                                                <Select v-model="formItem.jgA" multiple @on-change="SelectedControllerB">
                                                    <Option v-for="(item,index)  in getControllerB" :value="item.area_id+'-'+item.area_name" :key="'shiji'+index">{{ item.area_name}}</Option>
                                                </Select>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Form-item>
                        </i-form>
                        <div class="sift-bottom">
                            <div class="reset sift-btn" @click="reset">{{lang.reset}}</div>
                            <div class="sbt   sift-btn" @click="sbt">{{lang.sbt}}</div>
                        </div>
                    </div>
                </div>
            </transition>
