                </ul>







                <!-- 热点和无关键字搜索页面 -->
                <div class="search_pic" v-if="getHotData.length>0">
                    <div class="searchAdvanced">
                        <ul class="clearfix">
                            <li @click="personDetail(value)" v-if="index2<=hotsize" v-for="(value,index2) in getHotData" :key="index2">
                                <div class="carouselItem">
                                    <div class="img">
                                        <div class="img_wrap">
                                            <img :src="lazyload(value)" @error="errorImg($event)" />
                                        </div>
                                    </div>
                                    <h4>{{value.cn_name}}</h4>
                                    <span v-if="value.work_title && value.work_title!='无'">{{value.work_title}}</span>
                                    <span v-else></span>
                                    <span>{{value.college_name}}</span>
                                </div>
                            </li>
                        </ul>
                        <div v-if="this.hotsize<=this.getHotData.length" class="detailMore" @click="getHotMore()">
                            <span>{{lang.look_more}}</span>
                        </div>
                    </div>
                    <div class="searchKeyword" v-if="getHotData.length==0 &&  !getLoadingStatus">
                        <div class="searchResult">
                            <div class="searchResult_wraper">
                                <h4>{{lang.search_result}}</h4>
                                <p>{{lang.search_tip}}</p>
                            </div>
                        </div>
                    </div>
                    <div class="backTop" v-if="!getLoadingStatus && backTop_" @click="scrollTop"></div>
                </div>
                <div class="search_pic" v-else>
                    <div class="searchAdvanced">
                        <ul class="clearfix">
                            <li @click="personDetail(value)" v-for="(value,index2) in searchAdvanced" :key="index2">
