                                    <span v-if="value.work_title && value.work_title!='无'">{{value.work_title}}</span>
                                    <span v-else></span>
                                    <span>{{value.college_name}}</span>
                                </div>
                            </li>
                        </ul>
                        <div v-if="getAdvancedsearchTotalPage>1 && searchAdvanced.length>0" class="detailMore" @click="getMoreDetail()">
