<script>
import Vheader from '../../common/headerCommon.vue' //引入头部
import SearchKeyword from '../../../assets/js/searchKeyword' //引入全局函数
import Loading from '../../common/loadingSearch.vue'; //加载动画（这个单独放在seach页面，其他的都放在路由前面）

export default {
    name: 'search',
    data() {
        return {
            formItem: {
                arrowflg: {
                    xx: false,
                    xb: false,
                    nl: false,
                    xw: false,
                    xk: false,
                    gj: false,
                    jg: false,
                    dw: false,
                    zc: false,
                    hot: false,
                },
                state: {
                    smgj: false,
                    smdw: false,
                    smzc: false,
                    smxk: false,
                    smxw: false
                },
                value: {
                    gj: [],  //国籍
                    dw: [],  //单位
                    zc: [],  //职称
                    xw: [],  //学位
                    xk: [],  //学科
                    jg: [],   //籍贯
                    jgformat: [],//籍贯格式化
                    xb: "",   //性别
                    xbReturn: "" //性别（选中效果）
                },
                jgA: [],//地域1
                jgB: [],//地域2
                name: '',//姓名
                nameReturn: "", //姓名选中显示，不能直接读取name字段,因为姓名输入框选中效果需要点击会回车。
                age: { start: '', end: '', ageReturn: '' },//年龄单独放出来，不然会实时触发请求，因为他输入的是数字。

                ages: [],//年龄单独放出来，不然会实时触发请求，因为他输入的是数字
                gj_txt: '',//国籍
                dw_txt: '',//单位
            },
            searchRight_width: '', //搜索页面右边宽度获取 
            clientHeight: document.body.clientHeight ||  document.documentElement.clientHeight ,
            personalDetail: true, //查看更多
            personals: [],      //渲染数组   
            pagekey: 1, //关键字页数
            keyword: '',//关键字
            pagesAdvance: 0, //高级搜索页数
            size: 12, //默认12条数据
            currentPage: 0,       //页码
            haslistPage: 1,     //展示图片
            loading: false,
            optionsgj: [], //渲染页面的数据
            optionsdw: [], //单位
            hasMore: true,//还有下一页
            indeterminate: false,
            checkAll: false,
            indeterminate_: false,
            checkAll_: false,
            backTop_: false,
            hotsize: 11,
            lang: {},
            langType: "cn",
            rightMenu_flg: false,
            selectedParams: [],
            isRequest:{}
        }
    },
    watch: {
        "formItem.value": {
            handler(nv, ov) {
                this.$store.state.searchAdvancedList = []; //先清空本地数据--（出现图片加载延迟）
                this.size = 12; //初始化页数
                this.getparames(this.currentPage, this.size); //默认12条数据
                var params = this.getsearchAdvancedParams;//获取参数
                var count = 0;
                for (var item in params) {
                    count++
                };
                if (count > 2 || this.formItem.value.xb == '3') {  //当点击头部无关键字的时候 清空侧边条件（如果清空数据 watch会实时响应，所以做判断）
                    SearchKeyword.$emit("keyword", this.keyword);//去除头部搜索框关键字值
                    this.$store.commit('clearhotList'); //清空热点数据
                    this.$store.dispatch("Asynchronous_advanced", params);
                } else if (this.$store.state.hotListBACKUP.length > 0) {
                    //this.$store.state.hotListBACKUP 备份数据 
                    //当跳到搜索页面的时候且无关键字搜索那么备份赋值
                    this.$store.state.hotList = this.$store.state.hotListBACKUP
                } else if (this.$store.state.keyword == '') { //高级搜索页面内容条数如果为空 就重新请求无参接口
                    this.$store.commit("search_More");
                }
            },
            deep: true
        },
        "$route.fullPath": {
            handler() {
                this.getData();//从新请求热点数据
                this.init();
            }
        }
    },
    components: {
        Vheader, Loading
    },
    mounted: function () {
        this.init();
    },
    computed: {
        /*搜索*/
        searchAdvanced() {
            return this.$store.state.searchAdvancedList;
        },
        searchAdvancedLen() {
            return this.$store.state.searchAdvancedList.length;
        },
        /*籍贯*/
        getControllerA() {
            return this.$store.state.controllerA;
        },
        getControllerB() {
            return this.$store.state.controllerB;
        },
        /*国籍*/
        getCountrys() {
            return this.$store.state.countrys;
        },
        /*单位*/
        getCompanys() {
            return this.$store.state.companys;
        },
        /*职称*/
        getworkTitles() {
            return this.$store.state.workTitles;
        },
        /*学位*/
        getlearnDegrees() {
            return this.$store.state.learnDegrees;
        },
        /*学科*/
        getsubjects() {
            return this.$store.state.subjects;
        },
        //高级搜索侧边参数获取
        getsearchAdvancedParams() {
            return this.$store.state.searchAdvancedParams;
        },
        //loading
        getLoadingStatus: function () { //loading..
            return this.$store.state.fetchLoading;
        },
        //获取关键字
        getsetKeyword: function () {
            return this.$store.state.keyword;
        },
        //关键字总页数
        getkeysearchTotalPage: function () {
            return this.$store.state.keysearchTotalPage;
        },
        //高级搜索总页数
        getAdvancedsearchTotalPage: function () {
            return this.$store.state.AdvancedsearchTotalPage
        },
        //职称传过来的值 
        getsetworkTitlesparams: function () {
            return this.$store.state.workTitlesparams;
        },
        //首页国籍创过来的值
        getcountrsparams: function () {
            return this.$store.state.countrsparams
        },
        //首页籍贯传过来的值 
        getareasparams: function () {
            return this.$store.state.areaparams;
        },
        //首页学科传过的值 
        getxuekeparams: function () {
            return this.$store.state.xuekeparams
        },
        // 首页标记
        getsetflg: function () {
            return this.$store.state.flg;
        },
        getclickMoresize: function () {
            return this.$store.state.clickMoresize;
        },
        //获取总条数
        gettotalElements_: function () {
            return this.$store.state.totalElements_
        },

        //获取热点的数据
        getHotData: function () {
            return this.$store.state.hotList;
        },
        //获取侧边搜索菜单是否显示
        getShowMenu: function () {
            return this.$store.state.ShowMenuStatus;
        }
    },
    filters: {   //过滤
        filterNum: function (value) {
            return value.split('-')[1];
        }
    },
    methods: {
        filterReturn: function (alias) {
            if (alias != '') {
                var index_ = alias.indexOf('R');
                var alias_ = alias.substring(0, index_);
                switch (alias) {
                    case "nameReturn":
                        this.formItem[alias_] = '';
                        this.formItem[alias] = "";
                        this.sureDate();
                        break;
                    case "xbReturn":
                        this.formItem.value[alias_] = '';
                        this.formItem.value[alias] = "";
                        break;
                    case "ageReturn":
                        this.formItem[alias_].start = '';
                        this.formItem[alias_].end = '';
                        this.formItem[alias_][alias] = '';
                        this.sureDate();
                        break;
                }
            }
        },

        init() {
            this.toggleLang();
            this.initSearch_name(); //初始化高级搜索侧边栏目信息
            this.getworkTitles_();//获取职称，默认选中效果
            this.getcountrsparams_();//获取国籍，默认选中效果 
            this.getareasparams_();//获取地域，默认选中效果 
            this.getxuekeparams_();//获取学科 ，默认选中效果
            this.initData() //刷新默认读取数据;

            window.addEventListener('scroll', this.backTop)//回到顶部


        },

         pp:function(){
          console.log('aaa');
         },

        removeSelected: function (v, alias) {
            switch (alias) {
                case "nameReturn": case "xbReturn": case "ageReturn":
                    this.filterReturn(alias);
                    break;
                case 'gj': case 'dw':
                    var edata = [];
                    var that = this;
                    for (var i = 0; i < that.formItem.value[alias].length; i++) {
                        if (that.formItem.value[alias][i] != v) {
                            edata.push(that.formItem.value[alias][i])
                        }
                    };
                    that.formItem.value[alias] = edata;
                    var ele = document.querySelector('#' + alias);
                    var eleSelected = ele.querySelector('.ivu-select-selection');
                    var selected_s = eleSelected.querySelectorAll('.ivu-tag-checked');
                    var v_name = v.split('-')[1];
                    for (var i = 0; i < selected_s.length; i++) {
                        if (this.trim(v_name) == this.trim(selected_s[i].innerText)) {
                            selected_s[i].parentNode.removeChild(selected_s[i]);
                        }
                    };
                    break;
                case 'jg':
                    var that = this;
                    if (!this.isArray(that.formItem.value[alias])) {
                        // var ele = document.querySelector('#group_sj');
                        // var eleSelected = ele.querySelector('.ivu-select-selection');
                        // var selectV = eleSelected.querySelector('.ivu-select-input');
                        // selectV.style.display='none'
                        that.formItem.value.jgformat = []; //选中效果
                        that.formItem.value[alias] = []; //清空参数
                        that.formItem.jgA = []; //清空籍贯二级数据
                        that.formItem.jgB = []; //当二级市区全部清空的时候，一级省份也清空。
                        this.$store.state.controllerA = '';//清空籍贯一级请求数据
                        this.$store.state.controllerB = '';//清空籍贯二级请求数据
                        this.$store.commit("getController", 1); //初始化一级数据数据
                    } else {
                        var edata = [];
                        for (var i = 0; i < that.formItem.value[alias].length; i++) {
                            if (that.formItem.value[alias][i] != v) {
                                edata.push(that.formItem.value[alias][i])
                            }
                        };
                        that.formItem.value[alias] = edata;
                        that.formItem.value.jgformat = edata;
                        that.formItem.jgA = edata;
                        if (that.formItem.jgA.length == 0) {
                            that.formItem.jgB = []; //当二级市区全部清空的时候，一级省份也清空。
                            this.$store.state.controllerA = '';//清空籍贯一级数据
                            this.$store.state.controllerB = '';//清空籍贯二级数据
                            this.$store.commit("getController", 1); //初始化一级数据数据
                        };
                    };
                    break;
                default:
                    var edata = [];
                    var that = this;
                    for (var i = 0; i < that.formItem.value[alias].length; i++) {
                        if (that.formItem.value[alias][i] != v) {
                            edata.push(that.formItem.value[alias][i])
                        }
                    }
                    that.formItem.value[alias] = edata;
                    break;
            }
        },
        getArea_A: function () {//初始化市区数据
            var params_ = this.formItem.jgB;
            this.formItem.value.jg = this.formItem.jgB;
            if (!this.isArray(params_)) {
                var params_array = [];
                params_array.push(params_);
                this.formItem.value.jgformat = params_array;
                var params = params_.split('-')[0];
            } else {
                var params = params_
            }

            if (params != '' && params != null && params.length > 0) {
                this.$store.commit("getControllerB", params);
            };
        },
        SelectedControllerB: function (v) { //市区赋值 这里做了转换处理
            if (v && v != '') {
                var valuejg = this.formItem.value.jg;
                if (!this.isArray(valuejg)) {
                    this.formItem.value.jg = v;
                    this.formItem.value.jgformat = v;
                };
            };
        },
        remoteMethod(query) {//初始化国籍数据
            if (query !== '') {
                this.$store.commit("getCountrys", query);
                this.loading = true;
                var that = this;
                setTimeout(() => {
                    that.loading = false;
                    that.optionsgj = that.getCountrys;

                }, 200);

            } else {
                this.optionsgj = [];
            }
        },

        remoteColleges: function (query) {
            if (query !== '') {
                this.$store.commit("getcolleges", query);
                this.loading = true;
                var that = this;
                setTimeout(() => {
                    this.loading = false;
                    this.optionsdw = that.getCompanys;

                }, 200);
            } else {
                this.optionsdw = [];
            }
        },
        onColleges: function () {  //单位
            var params_v = this.formItem.gj_dw;
            if (params_v != '') {
                this.$store.commit("getcolleges", params_v); //初始化国籍数据
            }
        },
        initData: function () {
            var remotekeyword = this.$route.query.keyword;  //远程获取路由参数
            if (remotekeyword) {
                this.$store.commit('setKeyword', remotekeyword); //设置远程关键字  
                var keyword = this.getsetKeyword;
            } else {
                var keyword = this.getsetKeyword; //获取本地关键字
            }
            if (!keyword && this.getsetflg && this.getHotData.length == 0) {
                this.$store.commit("search_More");
            }
        },
        personDetail: function (v) {  //个人主页详情
            var map_name = v.mapping_name;
            if (map_name != '') {
                var url = 'http://10.203.8.48:8090/' + map_name;
                if (this.langType == "en") {
                    url = 'http://10.203.8.48:8090/en/' + map_name;
                }
                window.open(url);
            }
        },
        lazyload: function (obj) {
            if (!obj.imgUrl) {
                const app_secret = "6d3121b650e42855976d0f70dd2048e4";
                const app_key = "cgsoft";
                const timestamp = Date.parse(new Date());
                if (obj.uid) {
                    var uid = obj.uid;
                };
                const path = '/api/handler/p/' + uid;
                const sign = this.commonMethods.setSign(app_secret, app_key, timestamp, path);
                obj.imgUrl = this.config.serverUrl + path + "?app_key=" + app_key + "&timestamp=" + timestamp + "&sign=" + sign;
            }
            return obj.imgUrl;
        },
        initkeyPage: function () {
            this.pagekey = 1;
        },
        initSearch_name: function () {
            this.$store.commit("getController", 1); //初始化地域
            var objs = { name: 'work_title' };               //初始化职称数据
            this.$store.commit("getCommoncode", { objs });
            var objs = { name: 'learn_degree' };             //初始化学位数据
            this.$store.commit("getCommoncode", { objs });
            var objs = { name: 'subject' };                 //初始化学科数据
            this.$store.commit("getCommoncode", { objs });
        },
        sureDate: function () {  //搜索页面--输入框确定事件
         
           // SearchKeyword.$emit("keyword", this.keyword);//去除头部搜索框关键字值

            
            var params = this.DifferentiatedSearchParams();

            if(this.isRequest.firstNames!=params.firstNames || this.isRequest.ages!=params.ages){
                  console.log('aaaa')
                  this.$store.dispatch("Asynchronous_advanced", params);
            }else{
                console.log('bbbb')
            }

            this.isRequest={
                firstNames:params.firstNames ,
                ages:params.ages
             }


          
        },
        DifferentiatedSearchParams: function () {
            this.$store.commit('clearhotList'); //清空热点数据
            this.$store.state.searchAdvancedList = []; //清空本地数据--（原因:出现图片加载延迟）
            this.size = 12; //初始化页数
            this.getparames(this.currentPage, this.size); //参数赋值
            var params = this.getsearchAdvancedParams;//获取参数
            return params;
        },

        getMoreDetail: function () { //查看更多搜索数据
            if (this.getAdvancedsearchTotalPage != 1 && this.getAdvancedsearchTotalPage > 0) {
                this.size += 12;
                this.$store.commit('setclickMoresize', this.size);
                this.getparames(this.currentPage, this.getclickMoresize);
                var params = this.getsearchAdvancedParams;
                if (this.$store.state.keyword && this.$store.state.keyword != '') {
                    params.q = this.$store.state.keyword; //假如有关键字
                };
                var count = 0;
                for (var item in params) {
                    count++
                };
                if (count > 3) {
                    /*如果高级搜索侧边条件有3个以上条件（说明用户已经选择了条件参数，执行高级搜索接口）
                     清空关键字参数*/
                    params.q ? delete params.q : params;
                    this.$store.dispatch("Asynchronous_advanced", params);
                } else {
                    this.$store.dispatch("Asynchronous_advanced", params);
                };
            }
        },
        getHotMore: function () { //查看更多热点数据
            this.hotsize += 12;
        },
        getparames: function (page, size) {
            /*参数集合*/
        
            var params = {};
            /*姓名*/
            var name_ = this.formItem.name;
            if (name_) {
                params.firstNames = name_;
                this.formItem.nameReturn = name_; //姓名选中效果
                this.selectedParams.push(name_);
            };
            /*性别*/
            var xb_ = this.formItem.value.xb;
            if (xb_) {
                if (xb_ != '3') { // 3代表全部，也就是无参。
                    params.gender = xb_;
                };
                this.formItem.value.xbReturn = xb_ == '1' ? this.lang.male : xb_ == '2' ? this.lang.female : this.lang.all;//性别选中效果
                this.selectedParams.push(xb_);
            };
            /*年龄判断*/
            var age = this.formItem.age;
            var age_ = this.isages(age);
            if (age_) {
                params.ages = age_;
                var len = age_.split('~').length - 1;
                if (age_.split('~')[0] == "-1") {
                    this.formItem.age.ageReturn = age_.split('~')[1] + this.lang.ageDown; //年龄选中效果
                } else if (age_.split('~')[len] == '-1') {
                    this.formItem.age.ageReturn = age_.split('~')[0] + this.lang.ageUp; //年龄选中效果
                } else {
                    this.formItem.age.ageReturn = age_.split('~')[0] + " - " + age_.split('~')[1];  //年龄选中效果
                }
                this.selectedParams.push(this.formItem.age.ageReturn);
            };
            /*国籍 */
            var gj = this.formItem.value.gj;
            var gj_ = this.arraySplit(gj);
            if (gj_) {
                params.countrys = gj_;
                this.selectedParams.push(gj);
            };
            /*单位 */
            var dw = this.formItem.value.dw;
            var dw_ = this.arraySplit(dw);
            if (dw_) {
                params.companys = dw_;
                this.selectedParams.push(dw);
            };

            var zc = this.formItem.value.zc;
            var zc_ = this.arraySplit(zc);
            if (zc_) {
                params.workTitles = zc_;
                this.selectedParams.push(zc);
            };
            /*学位*/
            var xw = this.formItem.value.xw;
            var xw_ = this.arraySplit(xw);
            if (xw_) {
                params.learnDegrees = xw_;
                this.selectedParams.push(xw);
            };
            /*学科*/
            var xk = this.formItem.value.xk;
            var xk_ = this.arraySplit(xk);
            if (xk_) {
                params.subjects = xk_;
                this.selectedParams.push(xk);
            };
            /*籍贯*/
            var jg = this.formItem.value.jg;
            if (this.isArray(jg)) {
                var jg_ = this.arraySplit(jg);
            } else {
                var jg_array = [];
                jg_array.push(jg);
                var jg_ = this.arraySplit(jg_array);
            };
            if (jg_) {
                params.provinces = jg_;
                this.selectedParams.push(jg);
            };
            if (size) {
                params.size = size;
                params.page = page;
            };
            console.log(params)
            this.$store.commit("setsearchAdvancedParams", params); //所有的参数给vuex
        },
        isages: function (v) {  //年龄判读
            var v1 = v.start, v2 = v.end;
            if (isNaN(v1) || isNaN(v2)) {
                var that = this;
                this.$Modal.warning({
                    title: '',
                    content: '年龄请输入数字！',
                    onOk: function () {
                        that.formItem.age.start = '';
                        that.formItem.age.end = '';
                        that.formItem.age.ageReturn = '';
                    }
                })
                return false;
            };
            if (parseInt(v1) > parseInt(v2) && v2 != '') { //排序交换
                const v3 = v1;
                v1 = v2;
                v2 = v3;
            };
            if (v1 == '' && v2 != '') {
                v1 = -1
            };
            if (v2 == '' && v1 != '') {
                v2 = -1
            };

            if (v1 == '' && v2 == '') {
                return false
            };
            return v1 + "~" + v2;
        },
        arraySplit: function (array) { //数组分割
            if (array.length == 1) {
                var value_ = array[0].split('-')[0];
                return value_;
            } else {
                var values_ = [];
                for (var i = 0; i < array.length; i++) {
                    if (array[i].split('-')[0] != undefined && array[i].split('-')[0]) {
                        values_.push(array[i].split('-')[0]);
                    }
                };
                var values_string = values_.join(',');
                return values_string;
            }
        },
        errorImg(event) {
            const imgUrl = require('../../../assets/images/error.png');
            event.target.src = imgUrl;
        },
        getworkTitles_: function () {
            var otherParams = this.getsetworkTitlesparams;
            var name = otherParams.name;
            var key = otherParams.workkey;
            if (name && key) {
                if (key == '00,04,05,') {
                    if (this.langType != "cn") {
                        var workTitle1_ = '04,05,00' + '-' + 'Below Medium-Grade';
                    } else {
                        var workTitle1_ = '04,05,00' + '-' + '中级以下';
                    }
                    this.formItem.value.zc.push(workTitle1_);
                } else {
                    var workTitle_ = key + '-' + name;
                    this.formItem.value.zc.push(workTitle_);
                }
            };
        },
        getcountrsparams_: function () {
            var otherParams = this.getcountrsparams;
            var name = otherParams.name;
            var key = otherParams.codekey;
            if (name && key) {
                var countrs_ = key + '-' + name;
                this.formItem.value.gj.push(countrs_);
            }
        },
        getareasparams_: function () {
            var otherParams = this.getareasparams;
            var name = otherParams.name;
            var key = otherParams.codekey;
            if (name && key) {
                var area_ = key + '-' + name;
                this.formItem.value.jg.push(area_);
                this.formItem.value.jgformat.push(area_);
            }
        },
        getxuekeparams_: function () {
            var otherParams = this.getxuekeparams;
            var name = otherParams.name;
            var key = otherParams.workkey;
            if (name && key) {
                var xueke_ = key + '-' + name;
                this.formItem.value.xk.push(xueke_);
            }
        },
        backTop() {
        var  scrollTop = document.body.scrollTop ||  document.documentElement.scrollTop
            
           console.log('scrollTop ',scrollTop)


           console.log('sccc ',this.clientHeight )
            if (scrollTop > (this.clientHeight / 4)) {
                this.backTop_ = true;
            } else {
                this.backTop_ = false;
            };
           
        },
        scrollTop() {
     
           var heightTop = document.body.scrollTop ||  document.documentElement.scrollTop 

        

          var time = setInterval(function () {
                if (document.body.scrollTop != 0 || document.documentElement.scrollTop !=0 ) {
                   document.body.scrollTop =document.body.scrollTop  - 100;
                       document.documentElement.scrollTop = document.documentElement.scrollTop   - 100;
                } else {
                    clearInterval(time);
                }
            }, 20)
        },
        cleartAdvancedParams: function () {
            this.formItem.value.gj = [];
            this.optionsgj = [];//清空国籍请求后的数据
            this.formItem.value.dw = [];
            this.optionsdw = [];//清空单位请求后的数据
            this.formItem.value.zc = [];
            this.formItem.value.xw = [];
            this.formItem.value.xk = [];
            this.formItem.value.jg = [];
            this.formItem.value.jgformat = [];
            this.formItem.jgA = [];//清空籍贯
            this.formItem.jgB = [];//清空籍贯
            this.$store.state.controllerA = '';//清空籍贯一级数据
            this.$store.state.controllerB = '';//清空籍贯二级数据
            this.formItem.name = '';
            this.formItem.nameReturn = ''; //清空姓名选中效果
            this.formItem.age.start = '';
            this.formItem.age.end = '';
            this.formItem.age.ageReturn = '';//清空年龄选中效果
            this.formItem.gj_txt = '';
            this.formItem.value.xb = '';
            this.formItem.value.xbReturn = ''; //清空性别选中效果
        },
        toggleLang() {
            const _this = this;
            this.commonMethods.selectLangType(function (langType) {
                _this.lang = _this.langOption[langType];
                _this.langType = langType;
            })
        },
        getData: function () { //(中英文切换的时候，英文重新请求热点数据)
            var that = this;
            that.$store.dispatch("fetch_loading", true);
            this.commonMethods.emitAjax({
                path: "/api/front/psons/hot",
                type: "GET",
                data: {
                    count: 96
                },
                headers: null,
                success: function (response) {
                    that.$store.dispatch("fetch_loading", false);
                    that.$store.commit("sethotList", response); //所有的参数给sethotList
                }
            })
        },
        trim: function (str) { //去空格
            return str.replace(/(^\s*)|(\s*$)/g, "");
        },
        isArray: function (o) { //判断是否是数组类型
            return Object.prototype.toString.call(o) == '[object Array]';
        },
        closeAll: function (id) { //一键清空
            switch (id) {
                case 'jgA':
                    this.formItem[id] = [];
                    this.formItem.jgB = [];
                    this.formItem.value.jgformat = [];
                    this.formItem.value.jg = [];
                    this.$store.state.controllerA = '';//清空籍贯一级数据
                    this.$store.state.controllerB = '';//清空籍贯二级数据
                    this.$store.commit("getController", 1); //初始化一级数据数据
                    break;
                default:
                    this.formItem.value[id] = [];
                    break;
            }
        },
        hideMenu: function () {
            this.$store.commit('setShowMenuStatus', false); //移动端菜单是否显示
            document.querySelector('html').className = 'hairlines';
        },
        sbt: function () {
            this.sureDate();//提交完成
            this.hideMenu();
        },
        reset: function () {
            this.cleartAdvancedParams(); //清空数据
        }

    }
}
</script>



