<template>
    <!-- 门户首页 -->
    <header class="listHeader">
        <Icon type="ios-arrow-left" @click.native="backgo" class="backBtn"></Icon>
        <h3>{{headText}}</h3>
        <Icon type="navicon" @click.native="showMenu" v-if="rightBtn=='menu'" class="rightBtn"></Icon>
        <Icon type="ios-search" @click.native="searchKeyword" class="rightBtn" v-else></Icon>
    </header>
</template>
<script>
export default {
    data() {
        return {}
    },
    props: {
        headText: {
            type: String,
            default: "页面切换中"
        },
        rightBtn: {
            type: String,
            default: "search"
        }
    },
    computed: {

    },
    mounted: function () {

    },
    methods: {
        showMenu() {
            this.$store.commit('setShowMenuStatus', true); //搜索菜单显示--移动端
            document.querySelector('html').className = 'hairlines search-move';
        },
        searchKeyword() {},
        backgo() {
            this.$router.back();
        },
    }
}

</script>


