<template>
    <div class="rotates">
        <img src="./../../assets/images/loading.gif" alt="">
    </div>
</template>
<script>
export default {
  name: 'rotate',
  props: {
    personalDetail: {
      type: Boolean
    }
  }
}
</script>
<style scoped>
.rotates {
  margin-left: -60px;
  margin-top: -60px;
  position: absolute;
  bottom: 0px;
  bottom: 50%;
  left: 50%;
}
.rotates img {
  width: 120px;
  position: fixed;
  bottom: 50%;
}
</style>


